.App{
  width: 100%;
  min-height: 100vh;
  background-color: #e7e7e7;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Header{
  display: flex;
  position: fixed;
  background-color: #313440;
  top:0;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  width: 90%;
  padding: 0 5%;
  color:#fff;
}

.SearchForm{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 10vh;
}

.SearchInput{
  width: 40vw;
  height: 5vh;
  padding-left: 1vw;
  border: none;
  outline: none;
}
.SearchButton{
  background-color: #EE4C4C;
  color:#f8f8f8;
  margin-left: 1rem;
  padding: 0 1rem;
  height: 5vh;
  border:none;
  outline: none;
  transition: 1s;
}

.SearchButton:hover{
  background-color: #f8f8f8;
  color:#EE4C4C;
}



.Recipe{
  min-height: 25vw;
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: top;
  align-items: center;
  background-color: #313440;
  color:#fff;
  border-radius: 0.5rem;
  transition: 1s ease;
}
.Recipe:hover{
  box-shadow: 2px 2px 5px 5px grey;
}


.Recipe h1{
  text-align: center;
  font-size: 1.5rem;
  width: 80%;
  min-height: 10vh;
  overflow: hidden;
}
.Recipe img{
  width: 15vw;
  height: 15vw;
  padding-top: 3vh;
}


.RecipesContainer{
  width: 96vw;
  display: grid;
  justify-content: space-around;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap:3rem;
  padding-top: 12vh;
  padding-bottom: 2rem;
  padding-left: 2vw;
  padding-right: 2vw;

}


.ExtendedRecipe{
  width: 90%;
  height: 85vh;
  padding: 15vh 10%;
}

@media(max-width:1200px){
  .RecipesContainer{
    grid-template-columns: 1fr 1fr;
    width: 92vw;
    padding-left: 4vw;
    padding-right: 4vw;
  }
}


@media(max-width:700px){
  .RecipesContainer{
    grid-template-columns: 1fr;
    gap:1rem;
  }
  .Recipe{
    flex-direction: row;
    height: 30vh;
    width: 90%;
    margin: 0 5%;
  }
  .Recipe h1{
    max-height: 20vh;
    width: 70%;
  }
  .Recipe img{
    padding-top: 0;
    padding-left: 2rem;
  }
  .Header h1{
    font-size: 1rem;
  }
  .SearchForm{
    width: 70%;
  }
  .SearchInput{
    width: 30vw;
  }
  .SearchButton{
    padding: 0 0.5rem;
  }
}